.myNotesSearchInputField {
  margin-top: 20px;
  padding: 10px;
  font-family: poppins;
  font-weight: bold;
  font-size: 16;
  background-color: rgba(0, 0, 0, 0.05);
  color: black;

  outline: none;
  border: none;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  transition: all 0.2s;
  width: 300px;
}
.myNotesSearchInputField:focus {
  background-color: white;
  color: black;
  border-radius: 5;
  width: 320px;

  animation: example 1s ease;
}

@keyframes example {
  0% {
    transform: rotate(0deg);
    box-shadow: 0px 0px 10px black;
  }
}
